<template>
  <div
    id="scroll_main"
  >
    <div
      id="scroll"
      ref="refScroll"
      class="loading"
    >
      <ul
        id="list" 
        ref="refList"
      />
      <div
        id="fetchMore" 
        ref="refFetchMore"
      />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import { v4 as uuidv4 } from "uuid";
import { LoremIpsum } from "lorem-ipsum";
import { debounce } from './util.js'

export default {
  name: "InfiniteScrollDebounce",
  components: {
    // HelloWorld
  },
  data() {
    return {
      page: 0,
      list:[],
      ITEMS_PER_PAGE:20,
      lorem:null,
      listElem:null,
      timeOut:null,
      debounce:null,
    };
  },
  created() {
    //     .
    window.scrollTo(0, 0);
  },
  mounted() {
    ///////////////////////////////////
    this.lorem = new LoremIpsum({
      'wordsPerSentence': { min: 10, max: 30 },
    });
    ///////////////////////////////////
    this.listElem = this.$refs.refList;
    ///////////////////////////////////
    this.debounce = debounce(this.onScroll, 1000);






    //Default Call
    ///////////////////////////////////
    this.loadMore();
    ///////////////////////////////////
    // this.addDebounceScrollListner();
    window.addEventListener("scroll", this.debounce);
    // window.addEventListener("scroll", this.onScroll);
    // const myFunction = _.debo
    ///////////////////////////////////






  },
  unmounted() {
    ///////////////////////////////////
    window.removeEventListener("scroll", this.debounce);
    ///////////////////////////////////
  },
  methods: {
    // onScroll
    //       .
    //      loadMore  .
    onScroll() {
      console.log('Call onScroll()');
      const scrollHeight = document.getElementsByTagName("html")[0].offsetHeight;
      const scrollTop = window.top.pageYOffset;
      const clientHeight = window.top.innerHeight;
      if (scrollTop + clientHeight === scrollHeight) {
        this.loadMore();
      }
    },
    // onScroll
    //      .
    //  0   app  ,
    // 0     loading    
    // fetchMore  loading   .
    //  renderList  .
    async loadMore() {      
      const app = this.$refs.refScroll;
      const fetchMoreTrigger = this.$refs.refFetchMore;

      const target = this.page ? fetchMoreTrigger : app;
      target.classList.add("loading");
      await this.renderList(this.page++);
      target.classList.remove("loading");
    },
    // renderList()
    // dummyFetcher  
    //       
    //   getList  ,
    
    async renderList(page) {
      const list = await this.dummyFetcher(this.getList, page);      

      const frag = document.createDocumentFragment();
      list.forEach((item) => frag.appendChild(this.renderItem(item)));
      this.listElem.appendChild(frag);
    },



    dummyFetcher(method, args) {
      return new Promise(
        resolve => {
          this.timeOut = setTimeout(() => {
            resolve(method(args))
          }, 100);
        }
      );

    },




    
    getList(page = 0) {
      const list = [];
      list[page] = this.listBuilder(page);
      return list[page];
    },
    
    listBuilder(page) {
      return Array.from({ length: this.ITEMS_PER_PAGE }).map((_, i) =>
        this.itemBuilder(page * this.ITEMS_PER_PAGE + i + 1)
      );
    },


    renderItem({ id, no, text }) {
      const li = document.createElement("li");
      li.insertAdjacentHTML(
        "beforeend",
        `
      <div class="no">${no}</div>
      <div class="content">
        <div class="_id">${id}</div>
        <div class="text">${text}</div>
      </div>
    `
      );
      return li;
    },





    itemBuilder(no) {
      const obj = {
        id: uuidv4(),
        no: no,
        text: this.lorem.generateWords()
      }
      return obj;
    },
  }
};
</script>

<style>
#scroll_main {
  text-align: left;
}
#scroll {
  position: relative;
  padding: 10px 10px 0;
}
#scroll.loading {
  height: 100vh;
}
#scroll.loading:after {
  content: "...  ...";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-top: solid 1px #ccc;
}

#fetchMore {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin-top: 10px;
  display: none;
  text-align: center;
  line-height: 28px;
  border-top: solid 1px #000;
  border-bottom: solid 1px #000;
  background-color: #ff9;
}

#fetchMore.loading {
  display: block;
}
li {
  display: flex;
  flex-direction: row;
  border-bottom: solid 1px #ccc;
  font-size: 0.8em;
  color: #999;
}
.no {
  flex: 0 0 30px;
  padding: 10px 5px;
  text-align: center;
  place-self: center;
}
.content {
  padding: 10px 5px;
  border-left: 1px solid #ccc;
}
.text {
  font-size: 1em;
  color: #000;
  margin-top: 5px;
  line-height: 1.5em;
}
</style>
